import React from 'react'
import { connection } from '../Types/jsons'
import RouteStep from './RouteStep'

interface props {
    fastest: boolean
    price: string
    arrival: string
    connections?: Array<connection>
}

const RouteOverview = (props: props) => {
    console.log(props.connections);
  return (
      <div className='route-overview'>
        <h3>{props.fastest ? ("Fastest:") : ("Cheapest: ")}</h3>
        <p className='p1'>Price: {props.price} Arrival: {props.arrival}</p>
        <div className='route-step-container'>
            {props.connections && (
                props.connections.map((conn, index) => {
                    return (<RouteStep state={index === 0 ? -1 : 0} city={conn.from_city} type={conn.type} key={index} />);
                })
            )}
            {props.connections && <div><RouteStep state={1} city={props.connections[props.connections.length - 1].to_city} type={2} /></div> }
        </div>
      </div>
  )
}

export default RouteOverview