import React from 'react'
import logo from './../Components/icons/Logo_EITCo.png';
import dots from "./../Components/icons/hor-dots.png"

interface props {
  state: number
}

const Header = (props: props) => {
  return (
    <>
      <div className='header-grid'>
        <img src={logo} className="header-logo" alt="" />
        <div className={props.state == 0 ? "nav1" : "nav2"}>ENTER INFORMATION</div>
        <img src={dots} alt="" className='header-dots' />
        <div className={props.state == 1 ? "nav1" : "nav2"}>ORDER OVERVIEW</div>
        <img src={dots} alt="" className='header-dots' />
        <div className={props.state == 2 ? "nav1" : "nav2"}>ORDER CONFIRMATION</div>
      </div>
    </>
  )
}

export default Header