import React from 'react'
import { connection } from '../Types/jsons';
import RouteOverview from './RouteOverview'

interface props {
  fastestPrice: string,
  fastestArrival: string,
  fastestConnections: Array<connection>,
  bookFastest: () => void;
  cheapestPrice: string,
  cheapestArrival: string,
  cheapestConnections: Array<connection>,
  bookCheapest: () => void;
}

const RouteBooker = (props: props) => {
  return (
    <div>
      <RouteOverview fastest={true} price={props.fastestPrice} arrival={props.fastestArrival} connections={props.fastestConnections}></RouteOverview>
      <button className='button-small' onClick={props.bookFastest}>BOOK</button>
      <RouteOverview fastest={false} price={props.cheapestPrice} arrival={props.cheapestArrival} connections={props.cheapestConnections}></RouteOverview>
      <button className='button-small' onClick={props.bookCheapest}>BOOK</button>
    </div>
  )
}

export default RouteBooker