import React from 'react'
import RouteBooker from '../Components/RouteBooker';
import { booking_request, search_request, search_response } from '../Types/jsons';
import "./../App.css";

interface pageProps {
  sendBookingRequest: (req: booking_request, fastest: boolean) => void
  searchRequest?: search_request;
  cheapSearchResponse?: search_response;
  fastSearchResponse?: search_response;
}

const OverviewPage = (props: pageProps) => {
  const bookCheapest = () => {
    if (props.searchRequest && props.cheapSearchResponse){
      let req: booking_request = {
        price: props.cheapSearchResponse.price,
        duration: props.cheapSearchResponse.duration,
        start_date: props.searchRequest.start_date,
        package_weight: props.searchRequest.package_weight,
        package_dimension: props.searchRequest.package_dimension,
        connections: props.cheapSearchResponse.connections,
        description: props.searchRequest.description
      };
      console.log("booking cheapest")
      props.sendBookingRequest(req, false)
    }
  }
  const bookFastest = () => {
    if (props.searchRequest && props.fastSearchResponse){
      let req: booking_request = {
        price: props.fastSearchResponse.price,
        duration: props.fastSearchResponse.duration,
        start_date: props.searchRequest.start_date,
        package_weight: props.searchRequest.package_weight,
        package_dimension: props.searchRequest.package_dimension,
        connections: props.fastSearchResponse.connections,
        description: props.searchRequest.description
      };
      console.log("booking cheapest")
      props.sendBookingRequest(req, true)
    }
  }

  return (
    <div className="App order-grid-container">
      <div className='order-grid-item-from'>
          <h2>From</h2>
          <p className='p1'>{props.searchRequest?.from_city}</p>
      </div>
      <div className='order-grid-item-to'>
        <h2>To</h2>
        <p className='p1'>{props.searchRequest?.to_city}</p>
      </div>
      <div className='order-grid-item-weight'>
        <h2>Weight</h2>
        <p className='p1'>{props.searchRequest?.package_weight}</p>
      </div>
      <div className='order-grid-item-date'>
        <h2>Date of Shipping</h2>
        <p className='p1'>{props.searchRequest?.start_date}</p>
      </div>
      <div className='order-grid-item-dimensions'>
        <h2>Dimensions</h2>
        <p className='p1'>Height {props.searchRequest?.package_dimension.height} <span className='placeholder'>cm</span> </p>
        <p className='p1'>Width {props.searchRequest?.package_dimension.width} <span className='placeholder'>cm</span> </p>
        <p className='p1'>Depth {props.searchRequest?.package_dimension.depth} <span className='placeholder'>cm</span> </p>
      </div>
      <div className='order-grid-item-notes'>
        <h2>Notes</h2>
        <p className='p1'>{props.searchRequest?.description}</p>
      </div>
      <div className='order-grid-item-chosenorder'>
        {props.fastSearchResponse && props.cheapSearchResponse ? (
          <RouteBooker 
            fastestPrice={String(props.fastSearchResponse.price)} 
            fastestArrival={String(props.fastSearchResponse.arrival)} 
            fastestConnections={props.fastSearchResponse?.connections}
            bookFastest={bookFastest} 
            cheapestPrice={String(props.cheapSearchResponse.price)} 
            cheapestArrival={String(props.cheapSearchResponse.arrival)} 
            cheapestConnections={props.cheapSearchResponse.connections}
            bookCheapest={bookCheapest} />
        ): null}
      </div>
    </div>
  )
}

export default OverviewPage