import axios, { AxiosError } from "axios";

const API_URL = process.env.REACT_APP_API_URL;

export const api = axios.create({
  baseURL: API_URL,
  headers: {
    "Content-Type": "application/json",
  },
});

api.interceptors.request.use(
  async (request) => {
    console.log(`[API] HTTP.${request.method?.toUpperCase()} ${request.url}`);
    // request.headers!["X-Fifa-Contentful-Preview-Secret"] =
    //   localStorage?.getItem("PreviewModeToken") ?? "";
    return request;
  },
  (error) => {
    return Promise.reject(error);
  }
);

api.interceptors.response.use(
  (response) => {
    return response;
  },
  (error: AxiosError) => {
    if (error.response) {
      if (error.response.status === 401 || error.response.status === 403) {
        console.log("Response 401/403 from unknown endpoint");
      }
    }
    return Promise.reject(error);
  }
);

export default api;
