import React from 'react'
import InfoPage from '../Pages/InfoPage'
import OverviewPage from '../Pages/OverviewPage';
import ConfirmationPage from '../Pages/ConfirmationPage';
import { booking_request, booking_response, search_request, search_response } from '../Types/jsons';

interface managerProps {
    pageState: Number;
    cheapSearchResponse?: search_response;
    fastSearchResponse?: search_response;
    sendSearchRequest: (req: search_request) => void
    searchRequest?: search_request;
    sendBookingRequest: (req: booking_request, fastest: boolean) => void
    bookingRequest?: booking_request;
    bookingResponse?: booking_response;
    fastest?: boolean;
}

const PageManager = (props: managerProps) => {
    switch (props.pageState) {
        case 0:
            return (
                <>
                    <InfoPage sendSearchRequest={props.sendSearchRequest}></InfoPage>
                </>
            )    
        case 1:
            return (
                <>
                    <OverviewPage 
                        sendBookingRequest={props.sendBookingRequest}
                        searchRequest={props.searchRequest} 
                        cheapSearchResponse={props.cheapSearchResponse}
                        fastSearchResponse={props.fastSearchResponse}
                    ></OverviewPage>
                </>
            )
        case 2:
            return (
                <>
                    <ConfirmationPage
                        bookingResponse={props.bookingResponse}
                        bookingRequest={props.bookingRequest}
                        searchRequest={props.searchRequest}
                        fastest={props.fastest ? props.fastest : false}
                    ></ConfirmationPage>
                </>
            )
        default:
            return (
                <>
                    <h1>The Page State is broken</h1>
                </>
            )
    }
}

export default PageManager