import React from 'react';
import { useFormik } from "formik";
import { CITIES } from '../Types/cities';
import { search_request } from "../Types/jsons";

interface formProps {
    OnSubmit: (request: search_request) => void;
}

interface formError {
    package_weight?: string,
    start_date?: string,
    from_city?: string,
    to_city?: string,
    package_dimension?: {
        height?: number,
        width?: number,
        depth?: number
    },
    content_type?: Array<string>,
    anyErrors?: boolean,
}
const validate = (values: any) => {
    const errors: formError = {};
    //Weight Validation
    if (!values.packageWeight) {
        errors.package_weight = "required";
    } else if (isNaN(values.packageWeight)) {
        errors.package_weight = "weight is NaN"
    } else if (Number(values.packageWeight) > 100) {
        errors.package_weight = "greater than 100"
    } 
    //city verfication
    if (!CITIES.includes(values.from_city)){
        errors.from_city = "city doesn't exist";
    }
}

const BookingForm = (props: formProps) => {
    
    const formik = useFormik({
        initialValues: {
            package_weight: "",
            start_date: "",
            from_city: "",
            to_city: "",
            height: "",
            width: "",
            depth: "",
            description: "",
            fragile: "",
            live_animals: "",
            weapons: "",
            refridgerated_goods: ""
        },
        validate,
        onSubmit: values => {
            let request: search_request = {
                from_city: values.from_city,
                to_city: values.to_city,
                package_weight: Number(values.package_weight),
                start_date: values.start_date,
                package_dimension: {
                    height: Number(values.height),
                    width: Number(values.width),
                    depth: Number(values.depth)
                },
                content_type: [],
                description: values.description
            }
            if(values.fragile){
                request.content_type.push("fragile")
            }
            if(values.weapons){
                request.content_type.push("weapons")
            }
            if(values.live_animals){
                request.content_type.push("live_animals")
            }
            if(values.refridgerated_goods){
                request.content_type.push("refridgerated_goods")
            }
            props.OnSubmit(request);
        },
    });
    return (
      <form onSubmit={formik.handleSubmit}>
          <div className='form-grid-container'>

          <label> <h2>From * <br></br></h2>
          <input 
              type="text" 
              list='from_cities' 
              id="from_city" 
              name="from_city" 
              onChange={formik.handleChange}
              onBlur={formik.handleBlur} 
              value={formik.values.from_city} 
              placeholder="Please Select"
              className='input-dropdown form-grid-item-from'/>
            </label>
          <label> <h2>To * <br></br></h2>          
          <input 
              type="text" 
              list='to_cities' 
              id="to_city" 
              name="to_city" 
              onChange={formik.handleChange}
              onBlur={formik.handleBlur} 
              value={formik.values.to_city} 
              placeholder="Please Select"
              className='input-dropdown form-grid-item-to'/>
            </label>
          <datalist id="from_cities">
              <option>Cairo</option>
              <option>Suakin</option>
              <option>Addis Abeba</option>
              <option>Kap Guardafui</option>
              <option>Victoria Sen</option>
              <option>Zanzibar</option>
              <option>Mocambique</option>
              <option>Dragebjerget</option>
              <option>Kapstaden</option>
              <option>Kap St. Marie</option>
              <option>Amatave</option>
              <option>Hvalbugten</option>
              <option>Luanda</option>
              <option>Victoria Faldende</option>
              <option>Kabalo</option>
              <option>Congo</option>
              <option>Bahrel Ghazal</option>
              <option>Darfur</option>
              <option>Slave-Kysten</option>
              <option>Wadai</option>
              <option>Omdurman</option>
              <option>Timbuktu</option>
              <option>Sahara</option>
              <option>Guldkysten</option>
              <option>Sierra Leone</option>
              <option>Dakar</option>
              <option>Marrakesh</option>
              <option>De Kanariske er</option>
              <option>Tanger</option>
              <option>Tunis</option>
              <option>Tripoli</option>
              <option>St. Helena</option>
          </datalist>
          <datalist id="to_cities">
              <option>Cairo</option>
              <option>Suakin</option>
              <option>Addis Abeba</option>
              <option>Kap Guardafui</option>
              <option>Victoria Sen</option>
              <option>Zanzibar</option>
              <option>Mocambique</option>
              <option>Dragebjerget</option>
              <option>Kapstaden</option>
              <option>Kap St. Marie</option>
              <option>Amatave</option>
              <option>Hvalbugten</option>
              <option>Luanda</option>
              <option>Victoria Faldende</option>
              <option>Kabalo</option>
              <option>Congo</option>
              <option>Bahrel Ghazal</option>
              <option>Darfur</option>
              <option>Slave-Kysten</option>
              <option>Wadai</option>
              <option>Omdurman</option>
              <option>Timbuktu</option>
              <option>Sahara</option>
              <option>Guldkysten</option>
              <option>Sierra Leone</option>
              <option>Dakar</option>
              <option>Marrakesh</option>
              <option>De Kanariske er</option>
              <option>Tanger</option>
              <option>Tunis</option>
              <option>Tripoli</option>
              <option>St. Helena</option>
          </datalist>
          <label> <h2>Weight * <br></br></h2>
          <input 
              id="package_weight" 
              name='package_weight' 
              type="text" 
              onChange={formik.handleChange} 
              value={formik.values.package_weight} 
              onBlur={formik.handleBlur}
              placeholder="Max 100kg" 
              className='input-kg form-grid-item-weight'/>
            </label>
            <label> <h2>Date of Shipping * <br></br></h2>
            <input 
              id="start_date" 
              name='start_date' 
              type="date" 
              onChange={formik.handleChange} 
              value={formik.values.start_date} 
              className='input-kg form-grid-item-date'/>
            </label>
            <div className='form-grid-container-dimension'>
            <h2>Dimensions * <br></br></h2>
            <label> <h3>Height:</h3>
            <input 
              id="height" 
              name='height' 
              type="text" 
              onChange={formik.handleChange} 
              value={formik.values.height} 
              className='input-dimensions form-grid-item-dimension'
              placeholder='cm'/> 
            </label>         
            <label> <h3>Width:</h3>
            <input 
              id="width" 
              name='width' 
              type="text" 
              onChange={formik.handleChange} 
              value={formik.values.width} 
              className='input-dimensions form-grid-item-dimension'
              placeholder='cm'/>
            </label>
            <label> <h3>depth:</h3> 
            <input 
              id="depth" 
              name='depth' 
              type="text" 
              onChange={formik.handleChange} 
              value={formik.values.depth} 
              className='input-dimensions form-grid-item-dimension'
              placeholder='cm'/>
              </label>
            </div>            
            <div className='form-grid-container-checkboxes'>
            <h2>Contents</h2>
            <p className='p1'>Please check the boxes if your parcel can can be categorized by the following: </p>

            <label className="container-checkbox">Fragile 
            <input 
              id="fragile" 
              name='fragile' 
              type="checkbox" 
              onChange={formik.handleChange} 
              value={formik.values.fragile} 
              className='form-grid-item-checkboxes'/>
            <span className="checkmark"></span>
            </label>
            <br />
            <label className="container-checkbox">Live Animals 
            <input 
              id="live_animals" 
              name='live_animals' 
              type="checkbox" 
              onChange={formik.handleChange} 
              value={formik.values.live_animals} 
              className='form-grid-item-checkboxes'/>
            <span className="checkmark"></span>
            </label>
            <br />
            <label className="container-checkbox">Weapons 
            <input 
              id="weapons" 
              name='weapons' 
              type="checkbox" 
              onChange={formik.handleChange} 
              value={formik.values.weapons} 
              className='form-grid-item-checkboxes'/>
            <span className="checkmark"></span>
            </label>
            <br />
            <label className="container-checkbox">Refrigerated Goods
            <input 
              id="refridgerated_goods" 
              name='refridgerated_goods' 
              type="checkbox" 
              onChange={formik.handleChange} 
              value={formik.values.refridgerated_goods} 
              className='form-grid-item-checkboxes'/>
            <span className="checkmark"></span>
            </label>
            </div>
            <br />
            <label className='input-notes-label'> <h2>Notes</h2>
            <input 
              id="description" 
              name='description' 
              type="text" 
              onChange={formik.handleChange} 
              value={formik.values.description} 
              className='input-notes form-grid-item-notes'
              placeholder='transfer reason'/>
            </label>
            <br />
          <button className="button-large form-grid-item-search" type="submit">SUBMIT</button>
          </div>
      </form>
    )
}

export default BookingForm