import React, { useEffect, useState } from 'react';
import './App.css';
import Header from './Pages/Header';
import PageManager from './Utils/PageManager';
import VerticalLine from './Components/VerticalLine';
import api from './Utils/Api';
import { booking_request, booking_response, search_request, search_response } from './Types/jsons';
import LoadingSpinner from './Components/LoadingSpinner';

function App() {
  const [pageState, setPageState] = useState<number>(0);
  const [loading, setLoading] = useState<boolean>(false);
  const [fastestLoaded, setFastestLoaded] = useState<boolean>(false);
  const [cheapestLoaded, setCheapestLoaded] = useState<boolean>(false);
  const [searchRequest, setSearchRequest] = useState<search_request>();
  const [cheapSearchResponse, setFastSearchResponse] = useState<search_response>();
  const [fastSearchResponse, setCheapSearchResponse] = useState<search_response>();
  const [bookingRequest, setBookingRequest] = useState<booking_request>();
  const [fastestOrCheapest, setFastestOrCheapest] = useState<boolean>();
  const [apiError, setApiError] = useState<boolean>(false);
  const [bookingResponse, setBookingResponse] = useState<booking_response>();

  useEffect(() => {
    //create the abort controller
    const controller = new AbortController();

    if (bookingRequest){
      setLoading(true)
      api
        .post(`booking`, bookingRequest, {
          //save the signal so the abortcontroller can use it
          signal: controller.signal,
        })
        .then((res: any) => {
          if (res.data) {
            console.log(res.data)
            setBookingResponse(res.data)
          } else if (res.msg) {
            console.log(res.msg);
          }
        })
        .catch((err) => {
          console.log("Error: ", err);
        })
        .finally(() => {
          setPageState(2);
          setLoading(false);
          console.log("done processing data")
        });
    } else if (searchRequest){
      setLoading(true)
      //search for cheapest
      api
        .post(`search/cheap`, searchRequest, {
          //save the signal so the abortcontroller can use it
          signal: controller.signal,
        })
        .then((res: any) => {
          if (res.data) {
            console.log(res.data)
            setCheapSearchResponse(res.data)
          } else if (res.msg) {
            console.log(res.msg);
          }
        })
        .catch((err) => {
          console.log("Error: ", err);
          setApiError(true);
          alert("Invalid Input");
          window.location.reload();
        })
        .finally(() => {
          setCheapestLoaded(true);
        });
      //search for fastest
      api
        .post(`search/fast`, searchRequest, {
          //save the signal so the abortcontroller can use it
          signal: controller.signal,
        })
        .then((res: any) => {
          if (res.data) {
            console.log(res.data)
            setFastSearchResponse(res.data)
          } else if (res.msg) {
            console.log(res.msg);
          }
        })
        .catch((err) => {
          console.log("Error: ", err);
          setApiError(true);
          alert("Invalid Input");
          window.location.reload();
        })
        .finally(() => {
          setFastestLoaded(true);
        });
    }
  
      return () => {
        //abort api call if we end early
        controller.abort();
        console.log("Return from useEffect, ran landingPage/competitions getter");
      };
  }, [searchRequest, bookingRequest]);

  useEffect(() => {
    if (cheapestLoaded && fastestLoaded) {
      setLoading(false);
      setPageState(1)
    }
  }, [cheapestLoaded, fastestLoaded])
  

  const sendSearchRequest = (req: search_request) => {
    console.log(req);
    setSearchRequest(req);
  }
  const sendBookingRequest = (req: booking_request, fastest: boolean) => {
    console.log(req);
    setFastestOrCheapest(fastest)
    setBookingRequest(req);
  }

    return (
      <>
        <Header state={pageState}></Header>
        
        <PageManager pageState={pageState} 
            sendSearchRequest={sendSearchRequest} 
            sendBookingRequest={sendBookingRequest}
            searchRequest={searchRequest}
            cheapSearchResponse={cheapSearchResponse}
            fastSearchResponse={fastSearchResponse}
            bookingRequest={bookingRequest}
            bookingResponse={bookingResponse}
            fastest={fastestOrCheapest}
            ></PageManager>
        {loading && <LoadingSpinner></LoadingSpinner>}
    </>
  );
}

export default App;
