import React from 'react'
import { ReactComponent as ShipIcon } from "./icons/Icon_Ship.svg";
import { ReactComponent as PlaneIcon } from "./icons/Icon_Airplane.svg";
import { ReactComponent as TruckIcon } from "./icons/Icon_Truck.svg";
import { ReactComponent as HouseIcon } from "./icons/Icon_House.svg";


interface props {
    state: number; //-1 = departure, 0 = transfer, 1 = arrival
    city: string;
    type: number;
}

const RouteStep = (props: props) => {
    const handleStateText = () => {
        switch (props.state) {
            case -1:
                return "Departure: "
            case 0: 
                return "Transfer: "
            case 1:
                return "Arrival: "
            default:
                return "Broken State"
        }
    }
 
    const stateText: string = handleStateText();

  return (
      <div className='container-block'>
        <div className='route-step p1'><span className='route-step-text'>
            {stateText} {props.city} 
            </span>
            <span className='transport-icon'>
                {props.state === 1 ?
                    (<HouseIcon height="42px" />)
                    :(<>
                        {{
                            0: (<TruckIcon height="42px"/>),
                            1: (<ShipIcon height="42px"/>),
                            2: (<PlaneIcon height="42px"/>)
                        }[props.type]
                        }</>
                    ) 
                }
            </span>
        </div>
      </div>
  )
}

export default RouteStep