import React from 'react'
import "./../App.css";
import BookingForm from '../Components/BookingForm';
import { search_request } from '../Types/jsons';

interface pageProps {
  sendSearchRequest: (req: search_request) => void
}

const InfoPage = (props: pageProps) => {
  const OnSubmit = (req: search_request) => {
    props.sendSearchRequest(req);
  }
  return (
    <div className="App">
      <BookingForm OnSubmit={OnSubmit}></BookingForm>
    </div>
  )
}

export default InfoPage