import React from 'react'
import RouteOverview from "../Components/RouteOverview";
import { booking_request, booking_response, search_request } from '../Types/jsons';
import "./../App.css";

interface props {
  searchRequest?: search_request;
  bookingRequest?: booking_request;
  bookingResponse?: booking_response;
  fastest: boolean;
}

const ConfirmationPage = (props: props) => {
  const restart = () => {
    window.location.reload()
  }
  return (
    <div className="App order-grid-container">
    <div className='order-grid-item-from'>
        <h2>From</h2>
        <p className='p1'>{props.searchRequest?.from_city}</p>
    </div>
    <div className='order-grid-item-to'>
      <h2>To</h2>
      <p className='p1'>{props.searchRequest?.to_city}</p>
    </div>
    <div className='order-grid-item-weight'>
      <h2>Weight</h2>
      <p className='p1'>{props.searchRequest?.package_weight}</p>
    </div>
    <div className='order-grid-item-date'>
      <h2>Date of Shipping</h2>
      <p className='p1'>{props.searchRequest?.start_date}</p>
    </div>
    <div className='order-grid-item-dimensions'>
      <h2>Dimensions</h2>
      <p className='p1'>Height {props.searchRequest?.package_dimension.height} <span className='placeholder'>cm</span> </p>
      <p className='p1'>Width {props.searchRequest?.package_dimension.width} <span className='placeholder'>cm</span> </p>
      <p className='p1'>Depth {props.searchRequest?.package_dimension.depth} <span className='placeholder'>cm</span> </p>
    </div>
    <div className='order-grid-item-notes'>
      <h2>Notes</h2>
      <p className='p1'>{props.searchRequest?.description}</p>
    </div>
    <div className='order-grid-item-chosenorder'>
      <RouteOverview 
        connections={props.bookingRequest?.connections} 
        fastest={props.fastest} 
        price={String(props.bookingRequest?.price)} 
        arrival={String(props.bookingRequest?.start_date)}/>
    </div>
    <button className='button-large new-order-button' onClick={restart}>NEW ORDER</button>
  </div>
  )
}

export default ConfirmationPage